import {
  Motif,
  MotifColor,
  Stitch,
  StsGroup,
  getChartStsCount,
  getRightLeftFlippedChart,
  getXMergedChart,
} from "@tanyoknits/shared";

export function getMotifCombination(
  motifs: Motif[],
  edge: Stitch[],
  gutters: Stitch[][],
  motifColors: MotifColor[],
  centerMotif?: Motif
): Motif {
  let maxRowCount = Math.max(...motifs.map((m) => m.chart.length));
  if (centerMotif != null) {
    maxRowCount = Math.max(maxRowCount, centerMotif.chart.length);
  }

  const halfChart = Array.from({ length: maxRowCount }).map((_, i) => {
    return motifs.flatMap((m, j) => {
      const stsGroups: StsGroup[] = [];
      if (j === 0) {
        stsGroups.push({ sts: edge });
      }
      const motifRowID = i % m.chart.length;
      stsGroups.push({
        sts: m.chart[motifRowID].map((sg) => sg.sts).flat(),
        motif: { id: motifColors[j], isFirstRow: motifRowID === 0 },
      });
      stsGroups.push({ sts: gutters[j] });
      return stsGroups;
    });
  });

  const centerChart =
    centerMotif == null
      ? []
      : Array.from({ length: maxRowCount }).map((_, i) => {
          const rowID = i % centerMotif.chart.length;
          const row = centerMotif.chart[rowID];
          return [
            {
              sts: row.map((sg) => sg.sts).flat(),
              motif: {
                id: motifColors[motifs.length],
                isFirstRow: rowID === 0,
              },
            },
          ];
        });

  const halfChartFlipped = getRightLeftFlippedChart(halfChart);

  const chart = getXMergedChart([halfChart, centerChart, halfChartFlipped]);
  const stsCount = getChartStsCount(chart);

  return {
    chart,
    stsCount,
  };
}
