import {
  Abbr,
  ChartComponent,
  MotifColor,
  Techniques,
  definition,
  getMossMotif,
  techniques,
} from "@tanyoknits/shared";
import {
  getBasicBraidMotif,
  getZigZagMotif,
  getCenterDiamondMotif,
} from "@tanyoknits/shared";
import { MotifBySizeItem, PageInfo } from "../../utils/types";
import { sizes } from "./pattern";
import StitchPattern from "../../components/StitchPattern";
import { getMotifCombination } from "../../utils/patternCombination";
import MotifCombination from "../../components/MotifCombination";

// Stitches in charts
const { k, p, c6l, c6r, mb, c21lp, c21rp, m1l } = definition;

// Charts
const braidMotif = getBasicBraidMotif(9, 8);
export const braid: MotifBySizeItem[] = [
  {
    sizes: sizes,
    motif: braidMotif,
  },
];

const zigzagS = getZigZagMotif(3, 1, 6);
const zigzagM = getZigZagMotif(3, 2, 6);
export const zigzag: MotifBySizeItem[] = [
  {
    sizes: sizes.slice(0, 1),
    motif: zigzagS,
  },
  {
    sizes: sizes.slice(1, 2),
    motif: zigzagM,
  },
];

const diamondMotif = getCenterDiamondMotif();
export const diamond: MotifBySizeItem[] = [
  {
    sizes: sizes,
    motif: diamondMotif,
  },
];

const mossMotif = getMossMotif(2);
export const moss: MotifBySizeItem[] = [{ sizes: sizes, motif: mossMotif }];

const combiColors: MotifColor[] = ["blue", "pink", "green"];
const combiS = getMotifCombination(
  [braidMotif, zigzagS],
  [k, k, p, p],
  [[p, p], [p]],
  combiColors,
  diamondMotif
);
const combiM = getMotifCombination(
  [braidMotif, zigzagM],
  [k, k, p, p],
  [
    [p, p],
    [p, p],
  ],
  combiColors,
  diamondMotif
);

export const combination: MotifBySizeItem[] = [
  {
    sizes: sizes.slice(0, 1),
    motif: { ...combiS, glyphSize: "M" },
  },
  {
    sizes: sizes.slice(1, 2),
    motif: { ...combiM, glyphSize: "M" },
  },
];

const usedTechniques: {
  title: string;
  desc: string;
  video: string | undefined;
}[] = [
  {
    title: "Cable CO",
    desc: "Used for CO of each shoulder",
    video: undefined,
  },
  {
    title: "m1l & m1r",
    desc: "Make 1 left/right, used for arm inset increase",
    video: m1l.videos?.[0].url,
  },
  {
    title: "w&t",
    desc: "Wrap & turn; short row technique, my preference is German short row. ",
    video: techniques["Wrap & Turn"]?.videos?.[0].url,
  },
  {
    title: "Sewn on BO",
    desc: "Or tubular BO",
    video: techniques["Tubular BO"]?.videos[0].url,
  },
];

const REFLECT_TEXT =
  "Reflected version of this motif is also used to make a symmetrical pattern on front and back.";

const stitchPage: PageInfo[] = [
  {
    id: "braid-chart",
    label: "Braid",
    component: (
      <StitchPattern
        intro={REFLECT_TEXT}
        motifBySize={braid}
        stitches={[k, p, c6l, c6r]}
      />
    ),
  },
  {
    id: "zigzag-chart",
    label: "Zigzag",
    component: (
      <StitchPattern
        intro={REFLECT_TEXT}
        motifBySize={zigzag}
        stitches={[k, p]}
      />
    ),
  },
  {
    id: "diamond-chart",
    label: "Diamond",
    component: (
      <StitchPattern
        intro="Same motif but without bobbles are used in the back."
        motifBySize={diamond}
        stitches={[k, p, c21lp, c21rp, mb]}
      />
    ),
  },
  {
    id: "moss-chart",
    label: "Moss",
    component: (
      <StitchPattern
        intro="This motif is used on the side as front and back connects to the whole body."
        motifBySize={moss}
        stitches={[k, p]}
      />
    ),
  },
  {
    id: "pattern-combination",
    label: "Combination",
    component: (
      <StitchPattern
        intro="Braid, Zigzag, Diamond motives are horizontally placed and repeated vertically (x3, x2, x1 respectively) in the pattern."
        motifBySize={combination}
      />
    ),
  },
  {
    id: "techniques",
    label: "Techniques",
    component: <Techniques list={usedTechniques} />,
  },
  {
    id: "abbr",
    label: "Abbreviations",
    component: <Abbr list={["CO", "BO"]} />,
  },
];

export default stitchPage;
